<script>
import peerEvent from './peer-control'

export default {
  name: 'RemoteControl',
  data() {
    return {
      
    }
  },
  mounted() {
    let that = this
    peerEvent.on('add-stream', (stream) => {
      console.log(stream)
      this.play(stream)
    })

    window.onkeydown = function (e) {
      let data = {
        keyCode: e.keyCode,
        meta: e.meta,
        alt: e.alt,
        ctrl: e.ctrl,
        shift: e.shift
      }
      peerEvent.emit('robot', 'key', data)
    }
    window.onmouseup = function (e) {
      const {width, height} = that.$el.querySelector('#control').getBoundingClientRect()
      let data = {
        clientX: e.clientX,
        clientY: e.clientY,
        video: {
          width,
          height
        }
      }
      peerEvent.emit('robot', 'mouse', data)
    }
  },
  methods: {
    play (stream) {
      let video = this.$el.querySelector('#control')
      video.srcObject = stream
      video.onloadedmetadata = function () {
        video.play()
      }
    }
  },
}
</script>

<template>
  <div style="height: 100%">
    <video id="control"></video>
  </div>
</template>

<style scoped>
#control {width: 100%; height: 100%; object-fit: fill}
</style>