
<script>
const ipcRenderer = window.ipcRenderer

export default {
  name: '',
  data() {
    return {
      localCode: '',
      controlText: '',
      remoteCode: ''
    }
  },
  beforeDestroy() {
    // ipcRenderer.remove('control-state-change', this.handleControlState)
  },
  created() {
    this.login()
    ipcRenderer.on('control-state-change', this.handleControlState)
  },
  methods: {
    link () {
      ipcRenderer.send('control', this.remoteCode)
    },
    setLocalCode (code) {
      this.localCode = code
    },
    async login () {
      let code = await ipcRenderer.invoke('login')
      this.setLocalCode(code)
    },
    handleControlState (e, name, type) {
      let text = ''
      if (type == 1) {
        // 控制别人
        text = `正在控制${name}`
      } else if (type == 2) {
        // 被别人控制
        text = `${name}正在控制你的电脑`
      }
      this.controlText = text
    }
  },
}
</script>

<template>
  <div class="control" v-if="!controlText">
    <div class="code">
      你的控制码 {{localCode}}
    </div>
    <input type="text" v-model="remoteCode">
    <button @click="link">连接</button>
  </div>
  <div class="control-ing" v-else>
    {{controlText}}
    <input type="text" placeholder="hhhh">
  </div>
</template>

