let peerEvent = new EventEmitter()
peerEvent.on('robot', (type, data) => {
  if (type == 'mouse') {
    data.screen = {
      width: window.screen.width,
      height: window.screen.height
    }
  }
  ipcRenderer.send('robot', type, data)
})

export default peerEvent