import VueRouter from 'vue-router'
import Control from '../components/Control.vue'
import RemoteControl from '../components/RemoteControl.vue'

let routes = [
  {
    path: '/',
    component: Control
  },
  {
    path: '/remotecontrol',
    component: RemoteControl
  }
]

let router = new VueRouter({
  routes
})

export default router